import React, { useState } from "react";
import ItemsCarousel from "react-items-carousel";

import { useTheme } from "@material-ui/core/styles";
import { useMediaQuery, Button } from "@material-ui/core";

import FlowerItem from "../../Products/Components/FlowerItem";
import history from "./history";
import "./css/miniCarousel.css";

function MiniCarousel(props) {
    const [activeItemIndex, setActiveItemIndex] = useState(0);

    const chevronWidth = 60;

    const data = props.data;
    const carousel = props.data.carousel;
    const isImage = props.image;
    const title = props.data.title;
    const description = props.description;
    // const theWidth = props.theWidth;
    // space between the cards !
    const gutter = props.gutter ? props.gutter : 170;
    // number of card
    // maxSize must be 2>n
    // fRx
    const NOC = props.NOC;

    const theme = useTheme();
    const tablet = useMediaQuery(theme.breakpoints.up("md"));
    const mobile = useMediaQuery(theme.breakpoints.up("sm"));
    const web = useMediaQuery(theme.breakpoints.up("lg"));

    return (
        <div
            id="miniCarouselMainContainer"
            className="miniCarouselMainContainer"
            style={NOC === 3 ? { width: "65%" } : {}}
        >
            {isImage ? (
                <div className="miniCarouselTitleContainer">
                    <img
                        src={data.image}
                        alt="Tsetseg title"
                        className="miniCarouselTitle"
                    />
                </div>
            ) : (
                <div className="miniCarouselTitleContainerLeft">
                    <div className="geeksRFont">{title}</div>
                </div>
            )}
            {
                NOC ? (
                    <ItemsCarousel
                        infiniteLoop={true}
                        activePosition={"center"}
                        requestToChangeActive={setActiveItemIndex}
                        activeItemIndex={activeItemIndex}
                        numberOfCards={
                            web
                                ? NOC
                                : tablet
                                ? NOC - 1
                                : mobile
                                ? NOC - 1
                                : NOC - 2
                        }
                        gutter={gutter}
                        leftChevron={
                            <div
                                style={{
                                    fontSize: 50,
                                    color: "#AAA",
                                }}
                            >
                                {"<"}
                            </div>
                        }
                        rightChevron={
                            <div
                                style={{
                                    fontSize: 50,
                                    color: "#AAA",
                                }}
                            >
                                {">"}
                            </div>
                        }
                        outsideChevron
                        chevronWidth={chevronWidth}
                    >
                        {carousel.map((item, idx) => {
                            return (
                                <FlowerItem
                                    data={item}
                                    key={idx}
                                    width={props.width ? props.width : "200px"}
                                />
                            );
                        })}
                    </ItemsCarousel>
                ) : (
                    <div
                        style={{
                            overflowX: "scroll",
                            overflowY: "hidden",
                            whiteSpace: "nowrap",
                            marginTop: "20px",
                        }}
                    >
                        {carousel.map((item, idx) => (
                            <img
                                key={idx}
                                src={item.imgList[0]}
                                alt={"just a title fRx kk"}
                                style={{
                                    height: "275px",
                                    display: "inline-block",
                                    paddingRight: "20px",
                                }}
                            />
                        ))}
                    </div>
                )

                // <div className="slideContainer">
                //     <MyGridList cellHeight={"auto"} cols={3}>
                //         {carousel.map((item, idx) => (
                //         <GridListTile key={idx} cols={1}>
                //             <img src={item.imgList[0]} alt={"just a title fRx kk"} />
                //         </GridListTile>
                //         ))}
                //     </MyGridList>
                // </div>

                // <ItemsCarousel
                //     infiniteLoop={true}
                //     activePosition={"center"}
                //     requestToChangeActive={setActiveItemIndex}
                //     activeItemIndex={activeItemIndex}
                //     // numberOfCards={null}
                //     gutter={gutter}
                //     leftChevron={
                //         <div
                //             style={{
                //                 fontSize: 50,
                //                 color: "#AAA",
                //             }}
                //         >
                //             {"<"}
                //         </div>
                //     }
                //     rightChevron={
                //         <div
                //             style={{
                //                 fontSize: 50,
                //                 color: "#AAA",
                //             }}
                //         >
                //             {">"}
                //         </div>
                //     }
                //     outsideChevron
                //     chevronWidth={chevronWidth}
                // >
                //     {carousel.map((item, idx) => {
                //         return <FlowerItem data={item} key={idx} width={props.width ? props.width : "200px"} />;
                //     })}
                // </ItemsCarousel>
            }

            {description ? (
                <div className="miniCarouselDescriptionContainer">
                    <p className="miniCarouselDescription">
                        {data.description}
                    </p>
                    <Button
                        className="miniCarouselButton"
                        onClick={() =>
                            history.push(
                                `/ProductScreen`,
                                window.scrollTo(0, 0)
                            )
                        }
                    >
                        Дэлгэрэнгүй
                    </Button>
                </div>
            ) : (
                <></>
            )}
        </div>
    );
}

export default MiniCarousel;
